import './Header.css'
import headerLogo from '../../img/headerLogo.png'
import reactLogo from '../../img/reactLogo.svg'
import { NavLink } from 'react-router-dom'
import { createElement, FC } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {
  getAuthId,
  getAuthLogin,
  getIsAuth,
} from '../../redux/selectors/authSelectors'
import { signOut } from '../../redux/reducers/authReducer'
import { Col, Layout, Row } from 'antd'
import { MenuFoldOutlined, MenuUnfoldOutlined } from '@ant-design/icons'

type PropsType = {
  collapsed: boolean
  colorBgContainer: string
  setCollapsed: (collapsed: boolean) => void
}

export const Header: FC<PropsType> = ({
  collapsed,
  colorBgContainer,
  setCollapsed,
}) => {
  const { Header } = Layout
  const isAuth = useSelector(getIsAuth)
  const authLogin = useSelector(getAuthLogin)
  const authId = useSelector(getAuthId)

  const dispatch = useDispatch()

  const signOutD = () => {
    dispatch(signOut() as any)
  }

  let onClickSignOutSignIn = () => {
    if (isAuth) return signOutD()
    else return <NavLink to={'/Login'}></NavLink>
  }

  return (
    <Header style={{ padding: 0, background: colorBgContainer }}>
      <Row>
        <Col span={20}>
          {createElement(collapsed ? MenuUnfoldOutlined : MenuFoldOutlined, {
            className: 'trigger',
            onClick: () => setCollapsed(!collapsed),
          })}
        </Col>
        <Col span={4}>
          <div className='header-main__auth__container'>
            {isAuth ? (
              <NavLink
                className='header-main__auth__link btn'
                to={'/Profile/' + authId}
              >
                {authLogin}
              </NavLink>
            ) : null}

            <button
              className='header-main__auth__btn btn'
              onClick={onClickSignOutSignIn}
            >
              {isAuth ? 'Sign out' : 'Sign in'}
            </button>
          </div>
        </Col>
      </Row>
    </Header>
  )
}

