import { NavLink } from 'react-router-dom'
import './FriendItem.css'
import userAvatar from './../../../../img/userAvatar.jpeg'
import { PeoplesDataType } from '../../../../Types/Types'
import { FC } from 'react'

type PropsType = {
  state: PeoplesDataType
  getUserProfile: (userId: number) => void
}

const FriendItem: FC<PropsType> = ({
  getUserProfile,
  state: { id, photos, name },
}) => {
  return (
    <NavLink
      to={'/Profile/' + id}
      className='friendItem__conteiner'
      onClick={() => {
        getUserProfile(id)
      }}
    >
      <img
        src={photos.small ? photos.small : userAvatar}
        className='friendItem__avatar'
        alt='avatar'
      />
      <p className='friendItem__name'>{name}</p>{' '}
    </NavLink>
  )
}

export default FriendItem
