import { ThunkAction } from 'redux-thunk'
import { InferActionsType, ThunkType } from '../../Types/Types'
import { AppStateType } from '../redux-store'
import { getAuth } from './authReducer'

const INITIALIZED_SUCCESS = 'app/INITIALIZED_SUCCESS'
const ADD_GLOBAL_ERROR = 'app/ADD_GLOBAL_ERROR'

export type InitStAppReduserType = {
  initialized: boolean,
  globalError: any,
}

type ActionsType = InferActionsType<typeof actions>

let initialState: InitStAppReduserType = {
  initialized: false,
  globalError: null,
}

const appReducer = (state: InitStAppReduserType = initialState, action: ActionsType) => {
  switch (action.type) {
    case INITIALIZED_SUCCESS:
      return {
        ...state,
        initialized: true,
      };

    case ADD_GLOBAL_ERROR:
      return {
        ...state,
        globalError: action.error,
      };
    default:
      return state
  }
}


const actions = {
  initializedSuccess: () =>
    ({ type: INITIALIZED_SUCCESS } as const),

  addGlobalError: (error: any) =>
    ({ type: ADD_GLOBAL_ERROR, error } as const)
}



export const initializeApp = ():
  ThunkAction<void, AppStateType, unknown, ActionsType> => (dispatch) => {
    let promise = dispatch(getAuth())
    Promise.all([promise]).then(() => {
      dispatch(actions.initializedSuccess())
    })
  }

export const showGlobalError = (error: string): ThunkType<ActionsType> => async (dispatch) => {
  function resetGlobalError() {
    dispatch(actions.addGlobalError(null))
  }
  await dispatch(actions.addGlobalError(error))
  setTimeout(resetGlobalError, 9000)
}

export default appReducer
