import './PostForm.css'
import { FC } from 'react'
import { Formik, Form, Field, ErrorMessage } from 'formik'
import { formValidatorMessage } from '../../utils/validators'

export type PostFormValuesType = {
  messageText: string
}

type PropsType = {
  addPost: (post: string) => void
}

const PostFormComponent: FC<PropsType> = ({ addPost }) => {
  const submit = (
    values: PostFormValuesType,
    { setSubmitting }: { setSubmitting: (isSubmitting: boolean) => void }
  ) => {
    setTimeout(() => {
      addPost(values.messageText)
      setSubmitting(false)
    }, 400)
  }

  const Textarea = (props: any) => <textarea {...props} />

  return (
    <div>
      <Formik
        initialValues={{ messageText: '' }}
        validate={formValidatorMessage}
        onSubmit={submit}
      >
        {({ isSubmitting }) => (
          <Form>
            <Field
              name='messageText'
              as={Textarea}
              placeholder='What`s on your mind, today?'
              className='post__textarea'
            />
            <ErrorMessage
              name='messageText'
              component='div'
              className='_error'
            />
            <button
              type='submit'
              disabled={isSubmitting}
              className='post__btn btn'
            >
              Send
            </button>
          </Form>
        )}
      </Formik>
    </div>
  )
}

export default PostFormComponent
