import ProfileInfo from './ProfileInfo/ProfileInfo'
import PostForm from './PostForm/PostForm'
import Post from './Post/Post'
import './Profile.css'
import Wallpaper from './Wallpaper/Wallpaper'
import { FC } from 'react'
import { ProfileExtraStateType } from '../../Types/Types'
import { StatusType } from './ProfileInfo/ProfileStatus/ProfileStatus'
import { PFTopSegmentType } from './ProfileInfo/ProfileDataForm/ProfileForm/ProfileFormTopSegment'
import { useDispatch, useSelector } from 'react-redux'
import {
  getPostsData,
  getProfile,
  getProfileAboutMe,
  getProfileExtra,
  getProfileFullName,
  getProfileLookingForAJob,
  getProfilePhotos,
  getProfileStatus,
} from '../../redux/selectors/profileSelectors'
import {
  actions,
  updateUserStatus,
  savePhoto,
  saveProfileUpdate,
} from '../../redux/reducers/profileReducer'

type PropsType = {
  authUserId: number | null
  userId: number | null
}

const Profile: FC<PropsType> = ({ userId, authUserId }) => {
  const postsData = useSelector(getPostsData)
  const profile = useSelector(getProfile)
  const profileExtra = useSelector(getProfileExtra)
  const photos = useSelector(getProfilePhotos)
  const fullName = useSelector(getProfileFullName)
  const aboutMe = useSelector(getProfileAboutMe)
  const lookingForAJob = useSelector(getProfileLookingForAJob)
  const status = useSelector(getProfileStatus)

  const dispatch = useDispatch()

  const addPostD = (post: string) => {
    dispatch(actions.addPost(post) as any)
  }

  const updateUserStatusD = (status: StatusType) => {
    dispatch(updateUserStatus(status) as any)
  }

  const savePhotoD = (file: File): Promise<boolean> => {
    return dispatch(savePhoto(file) as any)
  }

  const saveProfileUpdateD = (profile: PFTopSegmentType) => {
    dispatch(saveProfileUpdate(profile) as any)
  }

  const updateProfileExtraStateD = (extraState: ProfileExtraStateType) => {
    dispatch(actions.updateProfileExtraState(extraState) as any)
  }

  const isOwner = !userId
  // eslint-disable-next-line eqeqeq
  const isOwnerById = userId == authUserId
  let postElements = postsData.map((post: any) => (
    <Post state={post} key={post.id} />
  ))

  return (
    <div className='profile-content'>
      <div>
        <Wallpaper />
        <ProfileInfo
          profile={profile}
          profileExtra={profileExtra}
          photos={photos}
          fullName={fullName}
          aboutMe={aboutMe}
          lookingForAJob={lookingForAJob}
          status={status}
          updateUserStatus={updateUserStatusD}
          isOwner={isOwner || isOwnerById}
          savePhoto={savePhotoD}
          saveProfileUpdate={saveProfileUpdateD}
          updateProfileExtraState={updateProfileExtraStateD}
        />
      </div>
      <PostForm addPost={addPostD} />
      {postElements}
    </div>
  )
}

export default Profile
