const sideBarReducer = (
  state: InitStSideBarReduserType = initialState,
  action: ActionsType
) => {
  switch (action.type) {
    case GET_PEOPLES_ITEMS:
      return {
        ...state,
        peoplesData: action.peoples,
      };
    default:
      return state
  }
}

const actions = {
  getPeoplesItems: (peoples: PeoplesDataType[]) => ({
    type: GET_PEOPLES_ITEMS,
    peoples,
  } as const)
}



export const getPeoples = (): ThunkType<ActionsType> => {
  return async (dispatch) => {
    let peoples = await userAPI.getUser(1, 10, '', true)
    dispatch(actions.getPeoplesItems(peoples.items))
  }
}

export default sideBarReducer
