export const formValidatorMessage = (values: PostFormValuesType) => {
  const errors = {} as any
  if (values.messageText.length < 1) {
    errors.messageText = ''
  } else if (values.messageText.length > 350) {
    errors.messageText = `Max length is 350 symbols`
  }

  return errors
}