import { authAPI, ResultCodeEnum, ResultCodeForCaptcha, securityApi } from '../../componets/API/API'
import { InferActionsType, ThunkType } from '../../Types/Types'

const SET_USER_DATA = 'auth/SET_USER_DATA'
const GET_CAPTCHA_URL_SUCCESS = 'auth/GET_CAPTCHA_URL_SUCCESS'
const HAS_ERROR = 'auth/HAS_ERROR'

export type InitStAuthReduserType = {
  id: number | null
  email: string | null
  login: string | null
  isAuth: boolean
  captchaUrl: string | null
  error: string | null

}

type ActionsType = InferActionsType<typeof actions>

let initialState: InitStAuthReduserType = {
  id: null,
  email: null,
  login: null,
  isAuth: false,
  captchaUrl: null, // if null, then captcha is not required
  error: null
}

const authReduser = (state: InitStAuthReduserType = initialState, action: ActionsType) => {
  switch (action.type) {
    case SET_USER_DATA:
    case GET_CAPTCHA_URL_SUCCESS:
    case HAS_ERROR:
      return {
        ...state,
        ...action.payload,
      }

    default:
      return state
  }
}

export const actions = {
  setUserData: (
    id: number | null, email: string | null, login: string | null, isAuth: boolean
  ) => ({
    type: SET_USER_DATA,
    payload: { id, email, login, isAuth },
  } as const),

  getCaptchaUrlSuccess: (captchaUrl: string | null) => ({
    type: GET_CAPTCHA_URL_SUCCESS,
    payload: { captchaUrl },
  } as const),

  hasError: (error: string) => ({
    type: HAS_ERROR,
    payload: { error }
  } as const)
}



export const getAuth = (): ThunkType<ActionsType> => async (dispatch) => {
  let authData = await authAPI.getAuth()

  let { id, email, login } = authData.data
  if (authData.resultCode === ResultCodeEnum.Success) {
    dispatch(actions.setUserData(id, email, login, true))
  }
}

export const signIn = (formData: object):
  ThunkType<ActionsType> =>
  async (dispatch) => {
    let loginData = await authAPI.signIn(formData)

    if (loginData.resultCode === ResultCodeEnum.Success) {
      // success, get auth data
      dispatch(getAuth())
    } else {
      if (loginData.resultCode === ResultCodeForCaptcha.CaptchaIsReuired) {
        // if required captcha, get captcha url
        dispatch(getCaptchaUrl())
      }

      let message =
        loginData.messages.length > 0
          ? loginData.messages[0]
          : 'some error'
      dispatch(dispatch(actions.hasError(message)))
    }
  }

export const signOut = (): ThunkType<ActionsType> => async (dispatch) => {
  let logoutData = await authAPI.signOut()

  if (logoutData.resultCode === ResultCodeEnum.Success) {
    dispatch(actions.setUserData(null, null, null, false))
  }
}

export const getCaptchaUrl = () => async (dispatch: any) => {
  let captchaUrl = await securityApi.getCaptchaUrl()
  dispatch(actions.getCaptchaUrlSuccess(captchaUrl))
}

export default authReduser
