import { FC, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { NavLink } from 'react-router-dom'
import { getUserProfile } from '../../redux/reducers/profileReducer'
import { getPeoples } from '../../redux/reducers/sideBarReducer'
import { getIsAuth } from '../../redux/selectors/authSelectors'
import { getFriends } from '../../redux/selectors/sideBarSelectors'
import FriendsFrame from './FriendsFrame/FriendsFrame'
import './SideBar.css'
import reactLogo from './../../img/reactLogo.svg'
import {
  UploadOutlined,
  UserOutlined,
  VideoCameraOutlined,
} from '@ant-design/icons'
import { Layout, Menu } from 'antd'

const { Sider } = Layout

type PropsType = {
  collapsed: boolean
}

export const SideBar: FC<PropsType> = ({ collapsed }) => {
  const peoples = useSelector(getFriends)
  const isAuth = useSelector(getIsAuth)

  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(getPeoples() as any)
  }, [dispatch])

  const getUserProfileD = (userId: number) => {
    dispatch(getUserProfile(userId) as any)
  }

  return (
    <Sider trigger={null} theme='light' collapsible collapsed={collapsed}>
      <div className={collapsed ? 'sideBar__logo_collapsed' : 'sideBar__logo'}>
        <img src={reactLogo} alt='reactLogo' />
        <div>React</div>
      </div>
      <Menu
        theme='light'
        mode='inline'
        defaultSelectedKeys={['1']}
        items={[
          {
            key: '1',
            icon: (
              <NavLink to='/Profile'>
                <UserOutlined />
              </NavLink>
            ),
            label: 'Profile',
            className: 'side-bar__item__link',
          },
          {
            key: '2',
            icon: (
              <NavLink to='/Dialogs'>
                <VideoCameraOutlined />
              </NavLink>
            ),
            label: 'Messages',
          },
          {
            key: '3',
            icon: (
              <NavLink to='/News'>
                <UploadOutlined />
              </NavLink>
            ),
            label: 'News',
          },
          {
            key: '4',
            icon: (
              <NavLink to='/Music'>
                <UserOutlined />
              </NavLink>
            ),
            label: 'Music',
          },
          {
            key: '5',
            icon: (
              <NavLink to='/Users'>
                <VideoCameraOutlined />
              </NavLink>
            ),
            label: 'Find friends',
          },
          {
            key: '6',
            icon: (
              <NavLink to='/Settings'>
                <UploadOutlined />
              </NavLink>
            ),
            label: 'Settings',
          },
        ]}
      />
      {collapsed ? null : (
        <FriendsFrame
          peoples={peoples}
          isAuth={isAuth}
          getUserProfile={getUserProfileD}
        />
      )}
    </Sider>
  )
}

