import { FC } from 'react'
import { PeoplesDataType } from '../../../Types/Types'
import FriendItem from './FriendItem/FriendItem'
import './FriendsFrame.css'

type PropsType = {
  peoples: PeoplesDataType[]
  isAuth: boolean
  getUserProfile: (userId: number) => void
}

const FriendsFrame: FC<PropsType> = ({ peoples, isAuth, getUserProfile }) => {
  let sideBarElements = peoples.map((people: PeoplesDataType) => {
    if (people.name.length < 16 && isAuth) {
      return (
        <FriendItem
          state={people}
          key={people.id}
          getUserProfile={getUserProfile}
        />
      )
    } else {
      return null
    }
  })

  if (isAuth) {
    return (
      <div className='fiendsFrame'>
        <h2 className='fiendsFrame__h2'>
          <span>Friends</span>
          <br /> and followers
        </h2>
        <div className='fiendsFrame__wrapper'>{sideBarElements}</div>
      </div>
    )
  } else {
    return null
  }
}
export default FriendsFrame
